export default function MediaVaultLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname()
  
  const isActiveTab = (href: string) => {
    if (href === '/sub-account/media-vault') {
      return pathname === href
    }
    return pathname.startsWith(href)
  }

  return (
    <div className="-m-4 sm:-m-6 lg:-m-8 overflow-hidden">
      {children}
    </div>
  )
}
